.dice-roll {
  text-align: center;

  &-btn {
    appearance: none;
    padding: 8px 16px;
    font-size: 24px;
    line-height: 1;
    vertical-align: middle;
    background: darkorange;
    color: lightyellow;
    border: none;
    cursor: pointer;

    &:hover {
      box-shadow: 0 1px 6px #999;
    }
  }

  &-result {
    height: 64px;
    font-size: 64px;
    line-height: 64px;
    vertical-align: middle;
    color: darkcyan;
    transition: all .3s ease-in;
  }

  &-rolling &-btn {
    box-shadow: none;
  }

  &-rolling &-result {
    font-size: 0;
  }
}



